.about-container{
    display:flex;
    background-color: white;
    color: black;
    padding: 50px 0 50px 0;
}

.about-desc{
    display: flex;
    flex-direction: column;
    justify-content:center;
    width:55%;
    padding: 0 100px 0 100px;
}

.about-desc > h3 {
    margin-bottom:30px;
    font-size:28px;
}

.about-desc > p{
    line-height: 1.5;

}

.about-img {
    width:45%;
    display: flex;
    align-self: center;
    overflow: hidden;
    object-fit: cover;
    border-radius: 1rem;
}

.about-img img {
    width: 800px;
    flex-direction: column;
    max-height: 100%;
    border-radius: 1rem;
    object-fit: cover;
}